.home-bg-container{
    background:url('https://assets.ccbp.in/frontend/react-js/home-lg-bg.png') ;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100vw;
    min-height: 125vh;
    
}

.first-part{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 10%;
    margin-bottom: 20px ;
}

.header-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 8%;
    background-color: rgb(24, 24, 24);
    color: white;
}

.home-website-logo{
    margin-left:10px ;
    width:15%;
    height: 10%;
}

.header-tabs{
    display: flex;
    /* justify-content: space-between; */
    align-items: center;
}

.home-heading{
    display: visible;
    margin-right: 50px;
}

.home-icon{
    display: none;
    margin-right: 20px;
}

.job-heading{
    display:visible;
}

.job-icon{
    display: none;
}

.logout-button{
    background-color: rgb(76, 48, 238);
    padding: 1%;
    display: visible;
    border: none;
    border-radius: 5px;
    justify-self: right;
    margin-right:10px ;
    color: white;
}

.logout-button-icon{
    display: none;
}

.body-container{
    display: flex;
    flex-direction: column;
    width: 35%;
    margin-left: 50px;
    margin-top: 120px;
    color: white;
}

.findjobs-button{
    padding: 10px;
    width: 80px;
    background-color: rgb(76, 48, 238);
    color: white;
    border: none;
    border-radius: 5px;
}

@media (min-width:320px) and (max-width:425px){
    .home-website-logo{
        margin-left:10px ;
        width:30%;
        height: 15%;
    }
}