.jobdetails-bg-cointainer{
    display:flex;
    flex-direction: column;
    background-color: black;
    max-width: 100vw;
    min-height: 100vh;
    color: white;
}

.first-part{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 10%;
    margin-bottom: 20px ;
}

.header-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 8%;
    background-color: rgb(24, 24, 24);
    color: white;
}

.home-website-logo{
    margin-left:10px ;
    width:15%;
    height: 10%;
}

.header-tabs{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.home-heading{
    display: visible;
    margin-right: 50px;
}

.home-icon{
    display: none;
    margin-right: 20px;
}

.job-heading{
    display:visible;
}

.job-icon{
    display: none;
}

.logout-button{
    background-color: rgb(76, 48, 238);
    padding: 1%;
    display: visible;
    border: none;
    border-radius: 5px;
    justify-self: right;
    margin-right:10px ;
    color: white;
}

.logout-button-icon{
    display: none;
}

.second-part{
    display: flex;
    flex-direction: column;
    align-items:center;
}

.second-part-1{
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    width: 80%;
    /* height:70%; */
    border-radius:15px;
    margin: 20px;
    padding:20px;
    background-color: rgb(24, 24, 24);
}

.secondpart-companylogo{
    display: flex;
    flex-direction: row;
    margin-bottom:20px;
    align-items: center;
}

.secondpart-logo{
    width: 10%;
    height:70%;
    margin-right:40px;
}

.secondpart-title-rating{
    display: flex;
    flex-direction:column;
}

.secondpart-loc-type-pack{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.break-3{
    width: 100%;
}

.des-vis{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.company-website{
    margin-top: 18px;
    font-size: larger;
    text-decoration: none;
    color: mediumslateblue;
    cursor: pointer;
}

.skill-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 20px;
}

.skill-part{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.skill-logo{
    width: 40%;
    height: 75%;
    margin-right: 20px;
}

.life-part{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.company-picture{
    margin:10px;
}

.similarjob-main{
    display:flex;
    flex-direction:row;
    flex-wrap: wrap;
    justify-content: center;
}

.similarjob-heading{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: flex-start;
}

.second-part-2{
    display: flex;
    flex-direction: column;
    width:20%;
    padding:20px;
    border-radius:15px;
    margin: 20px;
    margin-top: 0px;
    background-color: rgb(24, 24, 24);
}

.secondpartsecond-companylogo{
    display: flex;
    flex-direction: row;
}

.secondpartsecond-logo{
    width: 25%;
    height: 90%;
    padding-right: 20px;
}

.similar-loc-type{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

@media (min-width:320px) and (max-width:425px){
    .secondpart-logo{
        width: 25%;
        height:70%;
        margin-right:40px;
    }
    .secondpart-title{
        font-size: large;
    }
    .life-part{
        display: flex;
        flex-direction: column;
        /* justify-content: space-between; */
    }
    .company-picture{
        margin:10px;
    }
    .similarjob-main{
        display:flex;
        flex-direction:column;
        justify-content: center;
    }
    .second-part-2{
        display: flex;
        flex-direction: column;
        width:80%;
    }
    .similarjob-title{
        font-size: large;
    }
}