.login-bg-container{
    max-width: 100vw;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
}

.login-form-container{
    width: 350px;
    height: 300px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
}

.form-bg-container{
    padding: 20px;
    background-color: rgb(24, 24, 24);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-self: center;
    width: 50vw;
    max-height:80vh; 
} 

.login-website-logo{
    width: 150px;
    display: flex;
    align-self: center;
    padding: 10px;
}

.login-heading{
    color: white;
    font-size: 15px;
}

.login-fields{
    background-color: rgb(24, 24, 24);
    margin-top: 5px;
    margin-bottom: 15px;
    padding: 5px;
    color: white;
}

.login-button{
    background-color: rgb(76, 48, 238);
    margin-top: 10px;
    padding: 5px;
    color: white;
    border: none;
    border-radius: 5px;
}

.login-error{
    color: red;
    margin-top: 0px;
}
