.jobs-bg-container{
    max-width: 100vw;
    min-height: 100vh;
    background-color: black;
}

.first-part{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 10%;
    margin-bottom: 20px ;
}

.header-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 8%;
    background-color: rgb(24, 24, 24);
    color: white;
}

.home-website-logo{
    margin-left:10px ;
    width:15%;
    height: 10%;
}

.header-tabs{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.home-heading{
    display: visible;
    margin-right: 50px;
}

.home-icon{
    display: none;
    margin-right: 20px;
}

.job-heading{
    display:visible;
}

.job-icon{
    display: none;
}

.logout-button{
    background-color: rgb(76, 48, 238);
    padding: 1%;
    display: visible;
    border: none;
    border-radius: 5px;
    justify-self: right;
    margin-right:10px ;
    color: white;
}

.logout-button-icon{
    display: none;
}

.third-part{
    display: flex;
    flex-direction: column;
    width: 30%;
    height: 38px;
    margin-bottom: 20px;
    margin-left: 520px;
}

.job-search{
    display: flex;
    flex-direction: row;
    border: 1px solid grey;
    width:100%;
    height: 35px;
    border-radius: 5px;
}

.search-bar{
    background-color: black;
    color: white;
    padding: 8px;
    width:93%;
    max-height: 100%;
}
.search-bar:focus{
    outline:none;
}
.search-button{
    background-color: rgb(24, 24, 24);
    color: white;
    width: 7%;
    height: 100%;
    border: none;
}

.job-main-cointainer{
    display:flex;
    justify-content: space-between;
}

.job-first-part{
    color: white;
    display:flex;
    flex-direction: column;
    margin-left: 90px;
    width:30%;
    height: 100%;
}

.profile-bg-cointainer{
    display: flex;
    flex-direction: column;
    align-self: center;
    background-image: url('https://assets.ccbp.in/frontend/react-js/profile-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    width: 95%;
    height: 60%;
    border-radius: 10px;
    margin-bottom: 15px;
}

.profile-container{
    padding: 15px;
}

.profile-name{
    color: rgb(76, 48, 238);
}

.break-1{
    width: 95%;
}

.job-first-part-1{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}

.checkboxes{
    display: flex;
    flex-direction: row;
    padding: 10px;
}

.options{
    display: flex;
    flex-direction: row;
    padding: 10px;
}

.job-second-part{
    color: white;
    display: flex;
    flex-direction: column;
    width: 60%;
    min-height:100%;
    margin-right: 20px;
}

/* .job-cointainer{
    display: flex;
    flex-direction: column;
} */

.eachjob-container{
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
    background-color: rgb(24, 24, 24);
    border-radius: 15px;
    width: 94%;
    /* height: 2%; */
    padding: 20px;
}

.eachjob-companylogo{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}

.eachjob-logo{
    width: 10%;
    height:70%;
    padding-right: 20px;
}

.eachjob-title-rating{
    display: flex;
    flex-direction:column ;
}

.eachjob-loc-type-pack{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.break-2{
    width: 100%;
}

.cm-log{
    width:30%;
    height:20%;
}

.no-jobs-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}

.no-job-image{
    width:50%;
    height: 90%;
}

@media (width:768px){
    .third-part{
        margin-left: 330px;
    }
    .job-first-part{
        margin-left: 30px;
    }
    .search-button{
        width: fit-content;
    }
}

@media (min-width:320px) and (max-width:425px){
    .home-website-logo{
        margin-left:10px ;
        width:25%;
        height: 10%;
    }
    .third-part{
        margin-left: 50px;
        width: 75%;
    }
    .search-button{
        width: fit-content;
    }
    .job-main-cointainer{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .job-first-part{
        width: 80%;
        margin-left:0px;
    }
    .job-second-part{
        color: white;
        display: flex;
        flex-direction: column;
        width: 80%;
        min-height:100%;
        margin-right: 5px;
        margin-top: 20px;
    }
    .eachjob-container{
        display: flex;
        flex-direction: column;
        margin-bottom: 30px;
        background-color: rgb(24, 24, 24);
        border-radius: 15px;
        width: 92%;
        padding: 20px;
    }
    .eachjob-logo{
        width: 20%;
        height:70%;
        padding-right: 20px;
    }
    .job-title{
        font-size: medium;
    }
    
}
